  }
  .swiper-container--index .swiper-slide .banner-content .content__text h5 {
    font-size: 0.6rem;
  }
  .swiper-container--index .swiper-slide .banner-content .content__text .start {
    font-size: 0.5rem;
  }
  .swiper-container--index
.swiper-slide
.banner-content
.content__text
.start
svg {
    width: 12px;
  }
