footer {
  border-top: 3px solid #F89F0A;
  background-color: #0F245E;
  color: #ffffff;
}

footer .content {
  padding-top: 60px;
}

footer .content .b-e:not(:last-of-type) {
  border-inline-end: 4px solid #F89F0A;
}

footer .content .list li a {
  color: #ffffff;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

footer .content .list li a svg {
  width: 20px;
  height: 20px;
}

footer .content .list li a svg path {
  transition: all 0.3s ease-in-out;
}

footer .content .list li a:hover {
  color: #F89F0A;
}

footer .content .list li a:hover svg path {
  fill: #F89F0A;
  transition: all 0.3s ease-in-out;
}

footer .content .footer__social ul {
  display: flex;
}

footer .content .footer__social ul li {
  margin-inline-end: 5px;
  padding-inline-end: 5px;
}

footer .content .footer__social ul li:not(:last-of-type) {
  border-inline-end: 2px solid #F89F0A;
  transition: all 0.3s ease-in-out;
}

footer .content .footer__social ul li a {
  transition: all 0.3s ease-in-out;
}

footer .content .footer__social ul li a svg {
  width: 32px;
  height: 32px;
}

footer .content .footer__social ul li a svg path {
  transition: all 0.3s ease-in-out;
}

footer .content .footer__social ul li a:hover svg path {
  fill: #F89F0A;
  transition: all 0.3s ease-in-out;
}

@media screen and (min-width: 1400px) {
  footer .footer div {
    font-size: 1.3rem;
  }
  footer .footer div svg {
    width: 60px;
    height: 26px;
  }
}

@media (max-width: 767.98px) {
  footer .footer {
    flex-direction: column;
    text-align: center;
  }
  footer .footer div {
    padding: 5px;
  }
}
